import React from "react";
import Layout from "../components/Layout";
import Image from "../components/Image";
import { Helmet } from "react-helmet";

export default class Product extends React.Component {
  state = {
    size: "",
    name: ""
  };

  componentDidMount() {
    this.setState({ name: this.props.pageContext.name });
  }
  changeSize = event => {
    this.setState({ size: event.target.innerText });
  };
  render() {
    const { pageContext } = this.props;
    return (
      <Layout>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{pageContext.name} - Chibi Picks</title>
          <meta name="viewport" content="width=device-width, initial-scale=1" />
          <link rel="stylesheet" href="https://use.typekit.net/zgs7wbs.css" />
        </Helmet>
        <div className="product">
          <Image src={pageContext.imgs[0]} alt={pageContext.name} />
          <div className="product-details">
            <div className="product-name">{pageContext.name}</div>
            <div className="product-price">
              <span className="origin-price">
                {pageContext.price.toFixed(2)} EGP
              </span>
              <span>
                {(
                  pageContext.price -
                  Math.floor((pageContext.price * pageContext.discount) / 100)
                ).toFixed(2)}{" "}
                EGP
              </span>
            </div>
            <div className="sizes">
              {pageContext.sizes.map(size => {
                  console.log(this.state.size)
                return (
                  <span
                    key={size}
                    className={
                      "size-btn " + (this.state.size === size ? "selected" : "")
                    }
                    onClick={this.changeSize}
                  >
                    {size}
                  </span>
                );
              })}
            </div>
            <a href='https://m.me/320613145244475' className="details-btn btn-primary">
              Order Now
            </a>
          </div>
        </div>
      </Layout>
    );
  }
}
